"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import Image from "next/image"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"

export default function Home() {
  const [searchQuery, setSearchQuery] = useState("")
  const router = useRouter()

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault()
    if (searchQuery.trim()) {
      router.push(`/search?q=${encodeURIComponent(searchQuery.trim())}`)
    }
  }

  const handleLucky = () => {
    router.push("/lucky")
  }

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-4 space-y-6">
      {/* Google Logo */}
      <div className="mb-8 text-6xl font-bold tracking-tighter">
        <span className="text-[#4285F4]">G</span>
        <span className="text-[#EA4335]">o</span>
        <span className="text-[#FBBC05]">o</span>
        <span className="text-[#4285F4]">g</span>
        <span className="text-[#34A853]">l</span>
        <span className="text-[#EA4335]">e</span>
      </div>

      {/* Search Form */}
      <form onSubmit={handleSearch} className="w-full max-w-[584px]">
        <div className="relative mb-6">
          <Input
            type="text"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            placeholder="Search Google or type a URL"
            className="w-full h-12 px-12 rounded-full border border-gray-200 hover:shadow-md focus:shadow-md transition-shadow"
          />
          {/* Search icon */}
          <svg
            className="absolute left-4 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400"
            xmlns="http://www.w3.org/2000/svg"
            fill="none"
            viewBox="0 0 24 24"
            stroke="currentColor"
          >
            <path
              strokeLinecap="round"
              strokeLinejoin="round"
              strokeWidth={2}
              d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
            />
          </svg>
        </div>

        {/* Buttons */}
        <div className="flex justify-center space-x-4">
          <Button
            type="submit"
            variant="secondary"
            className="px-6 py-2 bg-[#f8f9fa] hover:bg-gray-200 text-gray-600"
          >
            Google Search
          </Button>
          <Button
            type="button"
            onClick={handleLucky}
            variant="secondary"
            className="px-6 py-2 bg-[#f8f9fa] hover:bg-gray-200 text-gray-600"
          >
            I&apos;m Feeling Lucky
          </Button>
        </div>
      </form>

      {/* Footer */}
      <footer className="fixed bottom-0 left-0 w-full bg-[#f2f2f2] text-sm text-gray-600">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-y-4 px-8 py-4">
          <div className="flex space-x-6">
            <a href="#" className="hover:underline">About</a>
            <a href="#" className="hover:underline">Advertising</a>
            <a href="#" className="hover:underline">Business</a>
            <a href="#" className="hover:underline">How Search works</a>
          </div>
          <div className="flex space-x-6 md:justify-end">
            <a href="#" className="hover:underline">Privacy</a>
            <a href="#" className="hover:underline">Terms</a>
            <a href="#" className="hover:underline">Settings</a>
          </div>
        </div>
      </footer>
    </main>
  )
}
